<style>
    .layers-tab{
        height: auto;
        max-height: 95%;
        overflow-y: scroll;
        border-radius: 2px;
    }
    .layers-tab::-webkit-scrollbar {
        width: 0px;
    }
    .element{
        width: 75%
    }
    .element:hover{
        transition: .1s;
        color: var(--main);
    }
    .active{
        color: var(--main);
    }
    
    /* Dark Mode */
    :global(body.dark-mode) .element{
      color: #fff;
    }
</style>

<div class="layers-tab">
    <div class="mx-2 my-3">
        <h6><i class="bi bi-layers"></i> <span style="vertical-align: middle;">Layers</span></h6><hr>
    </div>

    <div class="mx-2">
        {#each layers as layer}
            <div 
            class="col-12 element" 
            class:active="{$selectedElement == layer.el}"
            style="cursor: pointer; margin:10px {layer.spacing}px"
            on:mouseenter="{() => addOutline(layer.el)}"
            on:click="{() => selectElement(layer.el)}"
            on:mouseleave="{() => removeOutline(layer.el)}">
                <span>{layer.el.tagName}</span>
            </div>
        {/each}
    </div>
</div>

<script>
    import { mainDocument, loaded, selectedElement } from "../../../stores/main";
    import { addOutline, removeOutline } from "../../../scripts/design";
    import { activePage, pages } from "../../../stores/navigation";

    let layers = [];

    $: shouldRefresh = ($activePage == pages.LAYERS && $loaded);

    $: if(shouldRefresh){
        layers = []

        walkDom($mainDocument.body, (el, spacing) => {
            if(spacing) layers.push({el, spacing: spacing.length});
        })
    }

    function walkDom(el, callback, spacing = ""){
        if (callback(el, spacing) === false) return

        let node = el.firstElementChild

        while (node) {
            walkDom(node, callback, spacing + "---")

            node = node.nextElementSibling
        }
    }

    function selectElement(el){
        $selectedElement && $selectedElement.removeAttribute("editor-selected")

        if($selectedElement === el){
            $selectedElement = null;
        }else{
            $selectedElement = el;
            $selectedElement.setAttribute("editor-selected", "")
        }
    }
</script>