<style>
    :global(body.dark-mode) {
		--background: #0d1117;
		--background-secondary: #2e3135;
        --main: #df6b42;
        --main-secondary: #fff;
        --border: #2b2b2b94;
		color: #fff;
	}
    section{
        margin: 10px;
    }
    
    .toggle{
        text-align: right;
    }
    .toggle input[type=checkbox] {
        display: none;
    }
    .toggle label {
        background-color: #e2e2e2;
        border: 2px solid #f4f4f4;
        border-radius: 50px;
        cursor: pointer;
        display: inline-block;
        position: relative;
        transition: all ease-in-out 0.3s;
        width: 50px;
        height: 25px;
    }

    .toggle label::after {
        content: "🌞";
        background-color: #f4f4f4;
        border-radius: 50%;
        cursor: pointer;
        display: inline-block;
        position: absolute;
        left: -1px;
        top: -2px;
        transition: all ease-in-out 0.3s;
        width: 24px;
        height: 24px;
    }

    .toggle input[type=checkbox]:checked ~ label {
        background-color: var(--main);
        border-color: var(--border);
    }

    .toggle input[type=checkbox]:checked ~ label::after {
        content: "🌙";
        background-color: #fff;
        transform: translateX(23px) rotate(90deg);
    }
</style>

<section>
    <div class="form-group row">
        <div class="col-6">
            <label for="toggle">Dark mode</label>
        </div>
        <div class="col-6">
            <div class="toggle">
                <input type="checkbox" id="toggle" bind:checked="{darkMode}" on:input="{toggleDarkMode}"/>
                <label for="toggle"></label>
            </div>
        </div>
    </div>
</section>


<script>
    import { isDarkMode, toggleDarkMode } from "../../../../scripts/darkMode";

    let darkMode = isDarkMode();
</script>