<style>
  .search-holder{
      margin: 10px;
      margin-top: 20px;
  }
  .search-bar{
    background-color: var(--background-secondary);
    box-shadow: none;
    border: 2px solid var(--border);
    border-radius: 2px !important;
  }

  .elements{
      display: grid;
      grid-template-columns: 1fr 1fr;
      margin: 10px;
      margin-top: 20px;
      height: auto;
      max-height: 78%;
      overflow-y: scroll;
      border-radius: 2px;
      border-top: 0.1px solid var(--border);
      border-bottom: 0.1px solid var(--border);
  }
  .elements::-webkit-scrollbar {
      width: 0px;
  }
  .elements .card{
      border-radius: 2px;
      font-size: 15px;
      cursor: move;
      max-height: 120px;
      width: 100%;
  }
  .elements .image{
      font-size: 25px;
  }
  .elements .name{
      font-size: 15px;
  }

  /* Dark Mode */
  :global(body.dark-mode) .search-bar{
      color: #fff;
  }

  :global(body.dark-mode) .search-bar::placeholder { /* Chrome, Firefox, Opera, Safari 10.1+ */
    color: #fff;
  }
  
  :global(body.dark-mode) .search-bar:-ms-input-placeholder { /* Internet Explorer 10-11 */
    color: #fff;
  }
  
  :global(body.dark-mode) .search-bar::-ms-input-placeholder { /* Microsoft Edge */
    color: #fff;
  }
  :global(body.dark-mode) .elements .card{
      background-color: var(--background-secondary);
      color: #fff;
  }
</style>

<div class="search-holder">
    <input type="text" class="form-control search-bar" placeholder="Search elements" bind:value="{search}">
</div>

{#if activeElements.length}
    <div class="elements">
        {#each activeElements as element}
            <div class="card col-6" 
            transition:fade="{{ duration: 100 }}" 
            draggable="true" 
            on:dragstart="{onDragStart}"
            on:dragend="{onDragEnd}"
            data-element-id="{element.id}">
                <div class="text-center py-4">
                    <div class="image mb-1"><i class="{element.icon}"></i></div>
                    <div class="name"><span>{element.display_name}</span></div>
                </div>
            </div>
        {/each}
    </div>
{/if}

<script>
    import { fade } from 'svelte/transition';
    import { onDragStart, onDragEnd } from "../../../../scripts/dragDrop";
    import elementStore from "../../../../stores/elements"

    const elements = [
      {
          id: 1,
          name: "section",
          display_name: "Section",
          icon: "bi bi-square",
          template:"<section builder-section></section>"
      },
      {
          id: 2,
          name: "heading",
          display_name: "Heading",
          icon: "bi bi-fonts",
          template:"<h1>Hello</h1>"
      },
      {
          id: 3,
          name: "text",
          display_name: "Text",
          icon: "bi bi-paragraph",
          template:"<p>Text</p>"
      },
      {
          id: 4,
          name: "grid",
          display_name: "Grid",
          icon: "bi bi-columns",
          template:"<div editor-grid></div>"
      },
    ];

    const elementMap = new Map();

    elements.forEach(element =>{
        elementMap.set(element.id, element)
    });

    elementStore.set(elementMap);

    let search= "";

    $: activeElements = elements.filter((el) => el.display_name.toLowerCase().match(search.toLowerCase()));
</script>