<script>
    import { mainDocument } from "../../stores/main";
    import { onDragOver, onDrop, onDragEnter, onDragLeave } from "../../scripts/dragDrop";
    import { showWelcomeMessage } from "../../scripts/utils";

    function iframe(el){
        let timer = setInterval(function () {

            let frame = el.contentDocument || el.contentWindow.document;
            const isReady = ["complete", "interactive"].includes(frame.readyState);

            if (isReady) {
                $mainDocument = el.contentDocument

                $mainDocument.body.addEventListener("dragover", (event) => onDragOver(event))
                $mainDocument.body.addEventListener("drop", (event) => onDrop(event))
                $mainDocument.body.addEventListener("dragenter", (event) => onDragEnter(event))
                $mainDocument.body.addEventListener("dragleave", (event) => onDragLeave(event))

                clearInterval(timer);

                setTimeout(()=>{
                    showWelcomeMessage("Hi There 😃...")
                },1000);
            }

        }, 2000);
    }
</script>

<div class="col preview-holder">
    <div class="preview-main">
        <iframe title="editor-preview" 
        src="/preview/playground.html" 
        frameborder="0"
        allowTransparency="true"
        class="preview-window"
        use:iframe></iframe>
    </div>
</div>

<style>
    .preview-holder{
        border: 1px solid var(--border);
        margin: 10px 10px 3px 10px;
        border-radius: 3px;
        background-color: var(--background);
    }
    .preview-main{
        border: 1px solid var(--border);
        margin: 10px;
        height: calc(100% - 20px);
    }
    .preview-window{
        height: 100%;
        width: 100%;
    }
</style>