<style>
  .sidenav {
    height: 98%;
    width: 250px;
    background-color: var(--background);
    overflow-x: hidden;
    overflow-y: hidden;
    margin: 10px;
    border-radius: 3px;
    border: 2px solid var(--border);
  }
  .sidenav section{
    height: 100%
  }
</style>

<div class="sidenav">
    <section style={`display: ${$activePage == pages.ELEMENTS ? 'block': 'none'}`}>
     <Elements/>
    </section>

    <section style={`display: ${$activePage == pages.LAYERS ? 'block': 'none'}`}>
      <Layers/>
    </section>

    <section style={`display: ${$activePage == pages.ASSETS ? 'block': 'none'}`}>
      <Assets/>
    </section>

    <section style={`display: ${$activePage == pages.SETTINGS ? 'block': 'none'}`}>
      <Settings/>
    </section>
</div>

<script>
