import { mainProjectName , mainDocument, mainAssets } from "../stores/main"
import { postMessage } from "./broadcast"

function getProjectName(){
    let projectName = "";

    const unsubscribe = mainProjectName.subscribe((name) => {
        projectName = name
    });

    unsubscribe();

    return projectName
}

function getHTML(){
    let html = "";

    const unsubscribe = mainDocument.subscribe((doc) => {
        html = doc.body.innerHTML
        html = html ? html : ""
    });

    unsubscribe();

    return html
}

function getAssets(){
    let assets = "";

    const unsubscribe = mainAssets.subscribe((items) => {
        assets = items.map(({id, link}) => ({id, link}));
    });

    unsubscribe();

    return assets
}

export function getInitialData(){
    return {
        title: getProjectName(),
        html: getHTML(),
        assets: getAssets(),
    };
}

export function sendInitialData(){
    postMessage({action: "SETUP", payload: getInitialData()})
}

export function sendUpdatedTitle(){
    postMessage({action: "UPDATE_TITLE", payload: {
        title: getProjectName(),
    }})
}

export function sendUpdatedPreview(){
    postMessage({action: "UPDATE_PREVIEW", payload: {
        html: getHTML(),
        assets: getAssets(),
    }})
}