<style>
    .nav-tabs .nav-item .nav-link{
      cursor: pointer;
      background-color: var(--background-secondary);
      text-align: center;
      border-radius: 2px;
      border-color: transparent transparent var(--border) transparent;
    }
  
    .nav-tabs .nav-item .nav-link.active{
        border-radius: 2px;
        background-color: var(--background);
        border-color: transparent var(--border) transparent transparent;
    }

    /* Dark Mode */
    :global(body.dark-mode) .nav-tabs .nav-item .nav-link{
          color: #fff;
    }
    :global(body.dark-mode) .nav-link.active{
          color: #fff;
    }
    :global(body.dark-mode) .nav-tabs{
          border-color: var(--background);
    }
  </style>
  
  <div class="tabs">
      <ul class="nav nav-tabs">
          <li class="nav-item col-6" on:click="{goToDesign}">
              <span class="nav-link" class:active="{isDesigning}">Design</span>
          </li>
          <li class="nav-item col-6" on:click="{goToStyling}">
              <span class="nav-link" class:active="{!isDesigning}">Styles</span>
          </li>
      </ul>
  </div>
  
  {#if isPageDesign}

  <section class="h-100" in:fly="{{ x: -200 }}" out:fly="{{ x: -200 , duration: 0}}">
    <Design/>
  </section> 

  {:else} 

  <section class="h-100" in:fly="{{ x: 200 }}" out:fly="{{ x: 200, duration: 0 }}">
    <Styling/>
  </section>

  {/if}
  
  <script>
      import { fly } from 'svelte/transition';
      import Design from './Design/Design.svelte';
      import Styling from './Styling/Styling.svelte';

      let isDesigning = true;

      $: isPageDesign = isDesigning;

      function goToDesign(){
          if(!isDesigning){
            isDesigning = true;
          }
      }

      function goToStyling(){
          if(isDesigning){
            isDesigning = false;
          }
      }
  </script>