<style>
    .activity-bar{
        height: 100vh;
        width: 60px;
        background-color: var(--background);
        border-right: 2px solid var(--border);
    }
    .logo{
        display: block;
        font-size: 24px;
        text-align: center;
        border-radius: 50%;
        padding: 10px;
        margin: 6px;
        cursor: pointer;
    }
    .logo.circle{
        border: 1px solid var(--border);
    }
    .logo.tilted{
        transform: rotate(7deg);
        transition: .1s;
    }
    .logo a{
        text-decoration: none;
        color: black;
    }
    .logo i{
        display: grid;
    }
    .logo.tilted:hover{
        transform: rotate(20deg);
    }

    .logo.active{
        background-color: var(--main);
        color: #fff;
    }

    .drawer{
        text-align: center;
        margin-top: 20px;
    }

    .bottom-drawer{
        text-align: center;
        position: relative;
        bottom: 0;
    }

    /* Dark Mode */
    :global(body.dark-mode) .logo a{
        color: #fff;
	}
</style>

<div class="activity-bar">
    <div class="logo circle tilted">
        <a href="/"><i class="bi bi-layout-text-window-reverse"></i></a>
    </div>

    <div class="drawer">
        <div class="logo my-4" 
        title="Elements" 
        class:active="{($activePage == pages.ELEMENTS)}"
        on:click="{() => goToPage(pages.ELEMENTS)}">
            <i class="bi bi-front"></i>        
        </div>

        <div class="logo my-4" 
        title="Layers" 
        class:active="{($activePage == pages.LAYERS)}"
        on:click="{() => goToPage(pages.LAYERS)}">
            <i class="bi bi-layers"></i>
        </div>

        <div class="logo my-4" 
        title="Assets"
        class:active="{($activePage == pages.ASSETS)}"
        on:click="{() => goToPage(pages.ASSETS)}">
            <i class="bi bi-box-seam"></i>
        </div>
    </div>

    <div class="bottom-drawer">
        <div class="logo" 
        title="Settings" 
        class:active="{($activePage == pages.SETTINGS)}"
        on:click="{() => goToPage(pages.SETTINGS)}">
            <i class="bi bi-gear"></i>
        </div>
    </div>
</div>

<script>
    import { activePage, pages } from "../../stores/navigation";

    function goToPage(pageID){
        $activePage = pageID;
    }
</script>