import { sendInitialData } from "./preview"

export function startBroadcast(){
    const channel = new BroadcastChannel("editor-channel");

    channel.addEventListener("message", ({data}) => {
        switch(data.action){
            case "INIT": {
                sendInitialData();
                break;
            }
        }
    });

    window._EDITOR_CHANNEL_ = channel
}

export function postMessage(data){
    window._EDITOR_CHANNEL_.postMessage(data)
}

