export function addOutline(el){
    el.setAttribute("editor-outline", "")
}

export function removeOutline(el){
    el.removeAttribute('editor-outline')
}

export function addInsertPreview(el, position = "inside"){
    el.setAttribute("editor-insert-preview", position);
}

export function removeInsertPreview(el){
    el.removeAttribute("editor-insert-preview");
}

