<style>
  .input-bar{
    background-color: var(--background-secondary) !important;
    box-shadow: none !important;
    border: 2px solid var(--border) !important;
    border-radius: 2px !important;
  }
  :global(body.dark-mode) .input-bar{
      color: #fff;
  }

  :global(body.dark-mode) .input-bar::placeholder { /* Chrome, Firefox, Opera, Safari 10.1+ */
    color: #fff;
  }
  
  :global(body.dark-mode) .input-bar:-ms-input-placeholder { /* Internet Explorer 10-11 */
    color: #fff;
  }
  
  :global(body.dark-mode) .input-bar::-ms-input-placeholder { /* Microsoft Edge */
    color: #fff;
  }
</style>

<section class="setting-tab">
    <div class="mx-2 my-3">
        <h6><i class="bi bi-box-seam"></i> <span style="vertical-align: middle;">Assets</span></h6><hr>
    
        <div class="my-2 mt-4">
            <div class="col-12 row no-gutters">
                <div class="col-10 pe-0" style="display: block; align-self: center; vertical-align: middle;">
                    <input type="text" class="form-control input-bar" bind:value={linkInput} placeholder="CSS link">
                </div>
                <div class="col-1">
                    <button class="btn btn-main" on:click={handelAppendAsset} disabled={loading}>
                        <i class="bi bi-plus"></i>
                    </button>
                </div>
            </div>
        </div>

        <div class="my-3">
            {#each assets as asset, index}
                <div class="col-12 row no-gutters my-2">
                    <div class="col-10" style="display: block; align-self: center; vertical-align: middle;">
                        <a href={asset.link} target="_blank" style="text-decoration: none;">
                            <i class="bi bi-link-45deg"></i> {asset.name}
                        </a>
                    </div>
                    <div class="col-1">
                        <button class="btn btn-light text-danger" on:click={() => handelRemoveAsset(index)}>
                            <i class="bi bi-trash"></i>
                        </button>
                    </div>
                </div>
            {/each}
        </div>
    </div>
</section>

<script>
    import { mainDocument, mainAssets, loaded } from "../../../stores/main";
    import { loadCss } from "../../../scripts/utils";

    let loading = false;
    let assets = $mainAssets;

    let linkInput = "";

    function isURL(str) {
        let pattern = new RegExp('^(https?:\\/\\/)?'+ // protocol
        '((([a-z\\d]([a-z\\d-]*[a-z\\d])*)\\.?)+[a-z]{2,}|'+ // domain name
        '((\\d{1,3}\\.){3}\\d{1,3}))'+ // OR ip (v4) address
        '(\\:\\d+)?(\\/[-a-z\\d%_.~+]*)*'+ // port and path
        '(\\?[;&a-z\\d%_.~+=-]*)?'+ // query string
        '(\\#[-a-z\\d_]*)?$','i'); // fragment locator
        return !pattern.test(str);
    }

    function handelAppendAsset(){
        if(!linkInput) return;
        if(!isURL(linkInput)) return;

        let doesLinkAlreadyExists = assets.findIndex((({link}) => link == linkInput )) != -1
        if(doesLinkAlreadyExists) return 

        loading = true;

        fetch(linkInput).then(res => {
            if(res.status == 200){
                const element = loadCss($mainDocument, linkInput);

                assets = [...assets, {
                    id: `editor-asset-link-${Date.now()}`,
                    name: linkInput.split('\\').pop().split('/').pop(),
                    link: linkInput,
                    element
                }]

                $mainAssets = [...assets]
            }
        }).catch((err) => {
            console.log(err)
        }).finally(() => {
            linkInput = "";
            loading = false;
        })
    }

    function handelRemoveAsset(assetIndex){
        let asset = assets[assetIndex];

        asset.element.remove();

        assets = assets.filter((item, index) => index != assetIndex)

        $mainAssets = [...assets]
    }
</script>