<script>
  import ProjectName from "./ProjectName.svelte";
  import { sendUpdatedPreview } from "../../scripts/preview";

  let previewWindow = null;

  function handelPreview(){
    if(previewWindow && !previewWindow.closed){
      sendUpdatedPreview()
      previewWindow.focus()
    }else{
      previewWindow = window.open("/preview")
    }
  }

  function handelSave(){
    sendUpdatedPreview()
  }

  function handelDownload(){}
</script>

<nav class="navbar navbar-expand-lg navbar-light">
  <div class="container-fluid">
    <ProjectName />
    
    <button class="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarNav" aria-controls="navbarNav" aria-expanded="false" aria-label="Toggle navigation">
      <span class="navbar-toggler-icon"></span>
    </button>
    <div class="collapse navbar-collapse" id="navbarNav">
      <ul class="navbar-nav nav-left">
        <li class="nav-item">
          <button class="btn btn-main" on:click={handelSave}><i class="bi bi-save"></i> <span>Save</span></button>
          <button class="btn btn-main" on:click={handelDownload}><i class="bi bi-download"></i> <span>Download</span></button>
          <button class="btn btn-main" on:click={handelPreview}><i class="bi bi-eye"></i> <span>Preview</span></button>
        </li>
      </ul>
    </div>
  </div>
</nav>

<style>
  .navbar{
    background-color: var(--background);
  }
  .nav-left{
    margin-left: auto;
  }
</style>
