<script>
  import { mainProjectName } from "../../stores/main"
  import { sendUpdatedTitle } from "../../scripts/preview";

  let siteTitle = document.title;
  let projectName = "Untitled";
  let finalProjectName = $mainProjectName;
  let isEditable = false;

  function handelProjectName(){
    isEditable = true
  }
  
  function handelProjectNameEnter(event){
     if (event.code == 'Enter') {
        event.preventDefault()
        return saveProjectName();
     }
  }

  function saveProjectName(){
    isEditable = false
    
    if(!projectName.length) {
      projectName = "Untitled";
    }

    finalProjectName = projectName.trim()

    mainProjectName.set(finalProjectName);
    
    updateTitle();
  }

  function updateTitle(){
    const prefix = finalProjectName.length && finalProjectName != "Untitled" ? displayProjectName + " | " : "";

    document.title = (`${prefix} ${siteTitle}`).trim();

    sendUpdatedTitle(displayProjectName)
  }

  function focusInput(el){
    el.select();
    el.focus();
  }

  $: displayProjectName = projectName.length > 18 
                          ? projectName.substring(0, 15) + "..."
                          : projectName
</script>

{#if !isEditable}
    <span class="navbar-brand project-title-display" role="button" on:click="{handelProjectName}">
       {displayProjectName}
    </span>
{:else}
    <input type="text"
    class="project-title-input"
    bind:value={projectName} 
    on:blur="{saveProjectName}"
    on:keyup="{handelProjectNameEnter}" 
    use:focusInput>
{/if}

<style>
  .project-title-input{
    padding-top: .3125rem;
    padding-bottom: .3125rem;
    margin-right: 1rem;
    font-size: 1.25rem;
    text-decoration: none;
    white-space: nowrap;
    border: none;
    outline: none
  }

  /* Dark Mode */
  :global(body.dark-mode) .project-title-display{
        color: #fff;
	}
  :global(body.dark-mode) .project-title-input{
        color: #fff;
        background-color: var(--background);
	}
</style>